.history-container{
    padding-top: 100px;

    .search-container-batch{
        // border-radius: 50px;
        border: 1px solid #D6E1E6;
        background: var(--primary-white, #FFF);
        padding: 11px 25px 11px 25px;  
        display:flex;   
        width: 280px;
        justify-content: space-between;
        border-radius: 4px;

        
        input{
            border: none;
            font-size: 15px;
            color: #7E92A2;
    
    
        }
        input::placeholder{
            font-size: 15px;
            color: #7E92A2;
        }
        input:focus{
            outline: none;
        }
    }

    .history-header{
        display:flex;
        justify-content: space-between;
    }
    .history-right{
        display:flex;
        gap:24px;
        align-items: center;

        label{
            color: #6B7280;
            /* Body/Regular */
            font-family: Inter;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 30px; /* 187.5% */
        }
    }

    .search-container-batch{
        height: 24px;
    }
    
    .filter-records-text{
        color: #6B7280;
        /* Body/Regular */
        font-family: Inter;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 30px; /* 187.5% */
    }

    .history-left{
        display:flex;
        align-items: center;
        gap:35px;

        .filter-button{
            color: #000;
            text-align: center;
            /* Body/Regular */
            font-family: Inter;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 30px; /* 187.5% */
            border-radius: 10px;
            background: #F2F2F2;
            padding:6px 10px;
            cursor:pointer;
        }
        .filter-button:hover{
            opacity:0.8
        }
    }
    
}