.kyc-item {
    display: flex;
    .kyc-input p{
        display: block;
    }

    button {
        height: 37px;
        margin-top: 29px;
        border-radius: 5px;
        margin-left: 7px;
        border: none;
    }
    .status-button {
        width: 90px;
        flex-shrink: 0;
        color: #FFF;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 700;
        line-height: 224%;
        background: #CCC;
        border: none;
    }
    .status-button.pending {
        background: #FAC800;
    }
    .status-button.accepted {
        background: #3BC97B;
    }
    .status-button.rejected {
        background: #DC3545;
    }
    .eye-button {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        width: 37px;
        background: #008ECC;
        border: none;
    }
    .eye-button.slash {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        width: 37px;
        background: #909090;
        border: none;
    }
    .classification {
        color: #C0C0C0;
        font-family: Nunito;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 188.571%;
    }
    
    .kyc-type {
        width: 215px;
        height: 42px;
        flex-shrink: 0;
        border-radius: 4px;
        border: 1px solid #D6E1E6;
        p {
            color: #000;
            font-family: Inter;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 30px;
            padding-left: 16px;
            padding-top: 6px;
            padding-bottom: 6px;
        }
    }
    .status-container {
        display: flex;
        margin-top: 33px;
        margin-left: 14px;
        .status-result {
            margin-left: 6px;
        }
        width: 138px;
    }
    .status-text {
        color: #7E92A2;
        font-family: Nunito;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 187.5%;
    }
    .status-result {
        color: #00365A;
        font-family: Nunito;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 187.5%;
    }
    .kyc-checkbox {
        width: 25px;
        height: 25px;
        flex-shrink: 0;
        border-radius: 4px;
        border: 1px solid #D6E1E6;
        margin-top: 35px;
        margin-left: 105px;
    }
    .status-icon {
        margin-left: 12px;
        margin-bottom: 10px;
    }
    .separator {
        width: 856px;
        border: 1px;
        border-color: #EAEEF4;
        margin-top: 18px;
    }
}