.full-width-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  .header-row th {
    padding: 8px;
    // background-color: #f0f0f0;
    border-bottom: 1px solid #ccc;
    text-align: left;

    color: #7E92A2;
    font-family: Nunito;
    font-size: 16px;
    font-style: normal;
    font-weight: 800;
    line-height: 33.6px; /* 210% */
  }
  
  .full-width-table td {
    padding: 8px;
    border-bottom: 1px solid #ccc;
    text-align: left;
    color: #00365A;
    font-family: Nunito;
    font-size: 15px;
    font-style: normal;
    font-weight: 500;
    line-height: 30px; /* 200% */
  }

  .table-date-header{
    width: 200px;
  }
  
  .table-sizing{
    height: 450px;
    overflow:auto;
  }
  