
.schedule-batch-container{
    .schedule-batch{
        padding:100px;
    
        
    }
    .scheduler-container{
        display: flex;
        gap: 27px;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin: auto;
    }
    .batch-text{
        color: #6B7280;
        /* Body/Regular */
        font-family: Inter;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 30px; /* 187.5% */
    }

    .batch-select{
        color: #000;
        font-family: Inter;
        font-size: 15px;
        font-style: normal;
        font-weight: 400;
        line-height: 30px; /* 200% */
        border-radius: 4px;
        border: 1px solid #D6E1E6;
        padding: 10px 23px;
        width: 100%;


    }

    .batch-select:focus {
        outline: none;
      }

    .batch-date{
        border-radius: 4px;
        border: 1px solid #D6E1E6;
        padding: 10px 23px;
        color: #000;
        font-family: Inter;
        font-size: 15px;
        font-style: normal;
        font-weight: 400;
        width: 90%;
    }
    .batch-date:focus {
        outline: none;
      }

    .selector-width{
        width:60%;
    }

    .download-link {
        color: #5523D2;
        font-family: Inter;
        font-size: 16px;
        font-style: normal;
        font-weight: 600;
        line-height: 30px; /* 187.5% */
    }

    .schedule-batch-button-container{
        text-align: right;

        button{
            border-radius: 33px;
            background: #3BC97B;
            border: none;
            color: #FFF;
            font-family: Nunito;
            font-size: 15px;
            font-style: normal;
            font-weight: 400;
            line-height: 26.4px; /* 176% */
            padding: 9px 50px;
            cursor: pointer;


        }
        .disabled-color{
          background-color: grey;
        }


    }


   .upload-file-container{
    border-radius: 4px; 
    border: 1px solid #D6E1E6;
    padding: 9px 0px 9px 9px;

   }

   
.upload-container {
    padding: 50px 100px;
    overflow-x: auto; /* Allow horizontal scrolling for table */
  }
  
  .upload-file-container {
    border-radius: 4px;
    border: 1px solid #d6e1e6;
    padding: 9px;
    display: flex; /* Align file input and button */
    align-items: center;
  }
  
  /* Style the table to fit within the container */
  .upload-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  .upload-table th,
  .upload-table td {
    border: 1px solid #d6e1e6;
    padding: 8px;
    text-align: left;
  }
  
  /* Scrollbar styles for the table */
  .upload-container::-webkit-scrollbar {
    width: 8px;
  }
  
  .upload-container::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  
  .upload-container::-webkit-scrollbar-thumb {
    background: #888;
  }
  
  .upload-container::-webkit-scrollbar-thumb:hover {
    background: #555;
  }

  .csv-table-container{
    padding: 25px 60px;
    overflow-x: auto; /* Allow horizontal scrolling for table */
  }

  .schedule-batch-button-container button:hover {
    opacity: 0.8; /* Adjust the opacity value as desired (0.8 means 80%) */
}

.succuessful-schedule{
  color: green
}
  
}
