.information-container {
    display: block;
    flex-direction: column;
    align-items: center;
    .upload-button {
        width: 121px;
        height: 37px;
        flex-shrink: 0;
        border-radius: 5px;
        background: #008ECC;
        color: #FFF;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 700;
        line-height: 33.6px; /* 224% */
        border: none;
        margin-left: 50px;
        position: relative;
        top: -25px;
    }
}

.image-container {
    display: flex;

    .image-box {
        display: flex;
        align-items: center;
        width: 215px;

        img {
            max-width: 162px;
            height: auto;
            padding: 27px;
            border-radius: 4px;
            border: 1px solid #D6E1E6;
        }
    }

    .document-details {
        margin-left: 60px;
        margin-top: 23px;
        .document-line {
            display: flex;
            .document-fields{
                width: 216px;
                color: #7E92A2;
                font-family: Nunito;
                font-size: 16px;
                font-style: normal;
                font-weight: 500;
                line-height: 30px; /* 187.5% */
            }
            .document-fields-small{
                color: #7E92A2;
                font-family: Nunito;
                font-size: 11px;
                font-style: normal;
                font-weight: 500;
                line-height: 30px; /* 272.727% */
            }
            .document-data {
                color: #00365A;
                font-family: Nunito;
                font-size: 16px;
                font-style: normal;
                font-weight: 600;
                line-height: 30px; /* 187.5% */
            }
            .notes-input {
                width: 254px;
                height: 87px;
                flex-shrink: 0;
                border-radius: 4px;
                border: 1px solid #D6E1E6;
                resize: none;
                margin-top: 3px;
            }
        }
        .comment-block {
            display: flex;
            .submit-arrow {
                position: relative;
                width: 37px;
                height: 37px;
                flex-shrink: 0;
                border-radius: 5px;
                background: #DC3545;
                border: none;
                img {
                    filter: invert(1)
                }
                top: 58px;
                left: 6px;
            }
        }
    }
}