.report-filtering-options{
    color: #6B7280;
    /* Body/Regular */
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px; /* 187.5% */
    label{
        padding-right:30px;
    }

    select:focus{
        outline:none;
    }

    select{
        color: #6B7280;
        font-family: Inter;
        font-size: 15px;
        font-style: normal;
        font-weight: 400;
        line-height: 30px; /* 200% */
        padding:10px;
        min-width: 200px;
        min-height:49px;
        border-radius: 4px;
        border: 1px solid #D6E1E6;

    }

    input:focus{
        outline: none;
    }
    input{
        color: #6B7280;
        font-family: Inter;
        font-size: 15px;
        font-style: normal;
        font-weight: 400;
        line-height: 30px; /* 200% */
        // padding:10px;
        min-width: 200px;
        min-height:49px;
        padding-left: 10px;
        padding-right: 10px;
        border-radius: 4px;
        border: 1px solid #D6E1E6;

    }

    input::-webkit-calendar-picker-indicator {
        background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="15" viewBox="0 0 24 24"><path fill="%23bbbbbb" d="M20 3h-1V1h-2v2H7V1H5v2H4c-1.1 0-2 .9-2 2v16c0 1.1.9 2 2 2h16c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm0 18H4V8h16v13z"/></svg>');
      }
}

.print-report-button{
    // display:flex;
    text-align: end;

        img{
            height:25px;
            width:25px;
            cursor:pointer;

        
    }

    .print-report-button:hover{
        opacity: 0.8
    }

    // margin-top: -60px;
}