.kyc-header {
    display: flex;
    justify-content: space-between;
}

.security-warning {
    color: #DC3545;
    font-family: Nunito;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: 33.6px;
}

.kyc-compare {
    color: #5523D2;
    font-family: Nunito;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: 33.6px;
}

.ofac-container {
    display: flex;
    margin-top: 10px;
    .text-container {
        display: block;
        width: 215px;
    }
    .ofac-text {
        position: relative;
        color: #000;
        font-family: Nunito;
        font-size: 17px;
        font-style: normal;
        font-weight: 700;
        line-height: 197.647%;
        top: 5px;
    }
    .last-updated {
        color: #909090;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 500;
        line-height: 224%;
    }
    .run-button {
        position: relative;
        width: 62px;
        height: 37px;
        flex-shrink: 0;
        border-radius: 5px;
        background: #008ECC;
        margin-left: 21px;
        margin-top: 21px;
        border: none;
        color: #FFF;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 700;
        line-height: 224%;
        right: 12px;
    }
    .ofac-checkbox {
        margin-left: 21px;
        margin-top: 30px;
        width: 14px;
        height: 14px;
        flex-shrink: 0;
    }
    label {
        margin-top: 21px;
        color: #000;
        font-family: Nunito;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 33.6px;
        margin-left: 4px;
    }
    .drop-down-container {
        position: relative;
        display: block;
        margin-left: 32px;
        bottom: 10px;
        .drop-down-text {
            color: #6B7280;
            font-family: Inter;
            font-size: 12px;
            font-style: normal;
            font-weight: 400;
            line-height: 30px;
        }
        select {
            width: 120px;
            height: 36px;
            flex-shrink: 0;
            border-radius: 4px;
            border: 1px solid #D6E1E6;
        }
    }
}

.kyc-item {
    display: flex;
    .kyc-input p{
        display: block;
    }

    button {
        height: 37px;
        margin-top: 29px;
        border-radius: 5px;
        margin-left: 7px;
    }
    .status-button {
        width: 90px;
        flex-shrink: 0;
        color: #FFF;
        font-family: Nunito;
        font-size: 15px;
        font-style: normal;
        font-weight: 700;
        line-height: 224%;
        background: #CCC;
        border: none;
    }
    .eye-button {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        width: 37px;
        background: #008ECC;
        .eye-icon {
            filter: invert(1);
        }
        border: none;
    }
    .classification {
        color: #C0C0C0;
        font-family: Nunito;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 188.571%;
    }
    
    .kyc-type {
        width: 215px;
        height: 42px;
        flex-shrink: 0;
        border-radius: 4px;
        border: 1px solid #D6E1E6;
        p {
            color: #000;
            font-family: Inter;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 30px;
            padding-left: 16px;
            padding-top: 6px;
            padding-bottom: 6px;
        }
    }
    .status-container {
        display: flex;
        margin-top: 33px;
        margin-left: 14px;
        .status-result {
            margin-left: 6px;
        }
    }
    .status-text {
        color: #7E92A2;
        font-family: Nunito;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 187.5%;
    }
    .status-result {
        color: #00365A;
        font-family: Nunito;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 187.5%;
    }
    .kyc-checkbox {
        width: 25px;
        height: 25px;
        flex-shrink: 0;
        border-radius: 4px;
        border: 1px solid #D6E1E6;
        margin-top: 35px;
        margin-left: 105px;
    }
    .status-icon {
        margin-left: 12px;
        margin-bottom: 10px;
    }
    .separator {
        width: 856px;
        border: 1px;
        border-color: #EAEEF4;
        margin-top: 18px;
    }
}